<template>
        <!-- 性取向选择 -->
        <view class="box-in">
            <view class="box-in-in-3">
                <view :style="{
                    'opacity': requestParam.sexualOrientationCode == '' ? '.5' : '1',
                }" class="box-in-in-3-title">
                性取向-单选
                </view>
                <view class="box-in-in-3-list">
                    <view class="box-in-in-3-list-item" :style="{
                        'border': item.code == requestParam.sexualOrientationCode ? '5rpx solid #333' : '',
                    }" v-for="(item, index) in sexualOrientationList" :key="index"
                        @click="checkSexualOrientation(item.code)">
                        <view :style="{
                            'opacity': item.code == requestParam.sexualOrientationCode ? '1' : '.5',
                        }" class="box-in-in-3-list-item-text">{{ item.name }}</view>
                    </view>
                </view>
            </view>
        </view>
</template>

<script>
export default {
    data() {
        return {
            show:false,
            requestParam: {
                sexualOrientationCode: ""
            },
            /**
             * 性取向列表
             */
             sexualOrientationList: [
                {
                    "code": "male",
                    "name": "男性"
                },
                {
                    "code": "woman",
                    "name": "女性"
                },
                {
                    "code": "bisexual",
                    "name": "双性恋"
                },
                {
                    "code": "pansexual",
                    "name": "泛性恋"
                },
                {
                    "code": "nonsexuality",
                    "name": "无性恋"
                }
            ]
        }
    },

    mounted() {
        this.getSexualOrientationList();
    },

    methods:{
        open(){
            this.show = true;
        },
        close(){
            this.show = false;
        },
        checkSexualOrientation(sexualOrientationCode) {
            this.requestParam.sexualOrientationCode = sexualOrientationCode;
            this.$emit('checkSexualOrientation', sexualOrientationCode);
        },
        getSexualOrientationList() {
            this.$kt.request.request({
                api: this.$api.business.ovo.sexualOrientation.getList,
                stateSuccess: (res) => {
                    this.sexualOrientationList = res.data;
                }
            });
        }
    }
}
</script>

<style lang="scss" scoped>

.box-in {
    text-align: center;
    border-radius: 20rpx;

    .box-in-in-3 {
        border-radius: 10rpx;
        padding: 10rpx;

        .box-in-in-3-title {
            font-size: 30rpx;
            color: #666;
            text-align: left;
            margin-bottom: 20rpx;
            // margin-left: 10rpx;
        }

        .box-in-in-3-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
        }

        .box-in-in-3-list-item {
            width: calc(33.33% - 20rpx);
            margin-bottom: 20rpx;
            border: 5rpx solid #f0f0f0;
            padding: 30rpx 10rpx 30rpx 10rpx;
            border-radius: 20rpx;
            text-align: center;
            box-sizing: border-box;
        }

        .box-in-in-3-list-item-text {
            font-size: 30rpx;
            color: #333;
            opacity: .5;
        }
    }

}
</style>